const AccountScreen = (props: any) => {
  const { navigation, route } = props;
  const { signOut } = useAuth()
  const [isEnabled, setIsEnabled] = useState(false);
  const { t } = useTranslation();
 
  useEffect(() => {
    console.log(route);

  }, [])

  const handleSignOut = () => {
    signOut()
  }

  const toggleSwitch = async () => {
    setIsEnabled(previousState => !previousState);
    if (!isEnabled) {
      try {
        await AsyncStorage.setItem(storeFlag, 'true');
        Alert.alert("ng k FaceID thnh cng");
      } catch (error) {
        console.error('Error accessing AsyncStorage:', error);
        Alert.alert("ng k FaceID khng thnh cng");
      }
    } else {
      await AsyncStorage.removeItem(storeFlag)
      Alert.alert("Tt ng k FaceID thnh cng");
    }
  };

  const handlePress = () => {
    navigation.goBack();
  };

  return (
    <View style={{ flex: 1 }}>
      <View style={styles.headerView}>
        <View style={styles.subHeaderView}>
          <TouchableOpacity onPress={handlePress} style={[styles.backbtn, { marginBottom: 25 }]}>
            <Image source={image_back} style={styles.buttonImage} />
          </TouchableOpacity>
          <Text style={[styles.title, { marginBottom: 25 }]}>{t('settingScr.title')}</Text>
        </View>
        <Image source={image_icon_kita} style={styles.icon} />
      </View>

      <View style={styles.contentView}>
        <Text style={styles.texttitle}>{t('settingScr.acc')}</Text>
      </View>

      <TouchableOpacity style={{ height: 52, flexDirection: 'row', alignItems: "center", justifyContent: 'space-between' }}>
        <Text style={styles.notibtnlbl}>{t('settingScr.noti')}</Text>
        <TouchableOpacity onPress={() => { }} style={[styles.detailbtn]} >
          <Image source={image_detail} style={styles.buttonImage} />
        </TouchableOpacity>
      </TouchableOpacity>

      <View style={styles.contentView}>
        <Text style={styles.texttitle}>{t('settingScr.accSec')}</Text>
      </View>

      <TouchableOpacity style={{ height: 52, flexDirection: 'row', alignItems: "center", justifyContent: 'space-between' }}>
        <Text style={styles.notibtnlbl}>{t('settingScr.changePass')}</Text>
        <TouchableOpacity onPress={() => { }} style={[styles.detailbtn]}>
          <Image source={image_detail} style={styles.buttonImage} />
        </TouchableOpacity>
      </TouchableOpacity>

      <TouchableOpacity style={{ height: 52, flexDirection: 'row', alignItems: "center", justifyContent: 'space-between' }}>
        <Text style={styles.notibtnlbl}>{t('settingScr.faceID')}</Text>
        <Switch style={styles.switchTogger}
          trackColor={{ false: "#E6EAF0", true: "#62248F" }}
          thumbColor='white'
          ios_backgroundColor="#E6EAF0"
          onValueChange={toggleSwitch}
          value={isEnabled}
        />
      </TouchableOpacity>

      {/* <Button label='logout' onPress={handleSignOut} />
      <Button label='FaceID' onPress={handleFaceID} /> */}

    </View>
  )
}

